function createRouter(dict) {
    const result = []
    for (let key in dict) {
        let items = dict[key]
        for (let item of items) {
            let name = item['name']
            let title = item['title']
            result.push({
                name: name,
                path: '/' + name,
                img: require('./' + key + '/' + name + '.gif'),
                component: () => import('./' + key + '/' + name + '.vue'),
                meta: {
                    title: title,
                },
            })
        }
    }
    return result
}

export const gallery_demos = createRouter(dict)
